<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="SAP仓库" prop="name">
          <el-input v-model.trim="form.name" placeholder="请输入仓库名称" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="仓库名称" prop="warehouseSapId">
          <el-select v-model="form.warehouseSapId" filterable clearable placeholder="请选择" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in warehouseSapPullList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="地点描述" prop="placeDescription">
          <el-input v-model.trim="form.placeDescription" placeholder="请输入地点描述" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="地理位置" prop="geographicPosition">
          <el-input v-model.trim="form.geographicPosition" placeholder="请输入地理位置" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="所属车间" prop="cascaderValue">
          <el-cascader
            v-model="form.cascaderValue"
            :options="options"
            :props="{ expandTrigger: 'hover' }"
            style="width: 178px;"
            @change="handleChange"
          />
        </el-form-item>
        <el-form-item prop="type" label="类型">
          <el-radio-group v-model="form.type" style="width: 178px;">
            <el-radio-button v-for="(item, index) in [{name: '常规', value: 1},{name: '战略', value: 2}]" :key="index" :label="item.value">{{ item.name }}</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>
  </el-drawer>
</template>

<script>
import { getAddApi as addApi, getEditApi as updataApi, getWarehousePullList1Api } from '@/apis/modules/base/basewmsWarehouse'

const INIT_FORM = {
  name: '',
  geographicPosition: '',
  placeDescription: '',
  cascaderValue: [],
  warehouseArea: '',
  type: ''
}

export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM,
      loading: false,
      rules: {
        name: [
          { required: true, message: '请输入SAP仓库', trigger: 'blur' }
        ],
        placeDescription: [
          { required: true, message: '请输入地点描述', trigger: 'blur' }
        ],
        geographicPosition: [
          { required: true, message: '请输入地理位置', trigger: 'blur' }
        ],
        warehouseSapId: [
          { required: true, message: '请选择仓库名称', trigger: 'blur' }
        ],
        cascaderValue: [
          { required: true, message: '请选择所属车间', trigger: 'change' }
        ],
        type: [
          { required: true, message: '请选择类型', trigger: 'change' }
        ]
      },
      gradeOpts: [],
      warehouseSapPullList: []
    }
  },
  computed: {
    options() {
      const data = this.dict['wms_warehouse_area_type'].map(item => item)
      data.forEach(item => {
        item.children = this.dict['wms_warehouse_area_name'].filter(item1 => (item1.value.split('_')[0] && item.value === item1.value.split('_')[0]))
      })
      return data
    }
  },
  // 所属车间类别 所属车间名称
  dicts: ['wms_warehouse_area_type', 'wms_warehouse_area_name'],
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM }
        } else {
          // 根据二级分类找出一级分类
          const firstLevel = value.warehouseArea ? this.options.find(item => item.value === value.warehouseArea.split('_')[0]) : null
          // 进行回显
          this.form = { ...value, ...{ cascaderValue: value.warehouseArea ? [firstLevel.value, value.warehouseArea] : [] }}
        }
      },
      immediate: true
    }
  },
  mounted() {
    this.getwarehousePullList()
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = false
          const api = this.editType === 'modify' ? updataApi : addApi
          api({
            ...this.form,
            warehouseArea: this.form.cascaderValue.length && this.form.cascaderValue[this.form.cascaderValue.length - 1]
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}成功`,
                type: 'success'
              })
              this.$emit('confirm')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 下拉数据
    getwarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehousePullList1Api({}).then(res => {
          const { code, data } = res
          if (!code) {
            this.warehouseSapPullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    initSelect() {
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    },
    handleChange(val) {
      console.log(val)
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
}
::v-deep:focus {
  outline: 0;
}
.scroll-drawer {
  height: calc(100vh - 150px);
  // overflow: auto;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
::v-deep .el-drawer {
  width: 640px;
}
.drawer-footer{
  text-align: right;
}
</style>

