<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="sapName">
          <el-select v-model="query.sapName" filterable clearable placeholder="SAP仓库" style="width: 178px;" class="filter-item">
            <el-option
              v-for="(item,index) in sapWarehousePullList"
              :key="index"
              :label="item.name"
              :value="item.name"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="name">
          <el-select v-model="query.name" filterable clearable placeholder="仓库名称" style="width: 178px;" class="filter-item">
            <el-option
              v-for="(item,index) in warehousePullList"
              :key="index"
              :label="item.name"
              :value="item.name"
            />
          </el-select>
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="180"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="warning" icon="el-icon-edit" @click="handleModify(scope.row)">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <edit-dialog v-model="dialogVisible" :dialog-form="dialogForm" :edit-type="dialogType" :title="dialogTitle" @confirm="init(),getSapWarehousePullList()" />
  </div>
</template>
<script>
import { getListApi, getWarehouseSapPullListApi, getWarehousePullList1Api, getDeleteApi } from '@/apis/modules/base/basewmsWarehouse'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
export default {
  components: {
    EditDialog
  },
  mixins: [paginationPage],
  data() {
    return {
      query: {
        sapName: '',
        name: ''
      },
      dialogForm: {},
      title: this.$route.meta.title,
      loading: false,
      tableData: [],
      sapWarehousePullList: [],
      warehousePullList: [],
      dialogType: 'add',
      dialogVisible: false,
      columnList: [
        {
          module: 'word',
          prop: 'name',
          label: 'SAP仓库',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'warehouseSapName',
          label: '仓库名称',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'type',
          label: '类型',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (c) => c.type === 1 ? '常规' : '战略',
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'warehouseArea_dictText',
          label: '所属车间',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'placeDescription',
          label: '地点描述',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'geographicPosition',
          label: '地理位置',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'factoryCodeName',
          label: '工厂',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'dataSources_dictText',
          label: '数据来源',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  computed: {
    dialogTitle() {
      if (this.dialogType === 'add') {
        return `新增${this.title}`
      } else {
        return `编辑${this.title}`
      }
    }
  },
  mounted() {
    this.init()
    this.getSapWarehousePullList()
    this.getWarehousePullList()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 新增
    handleAdd() {
      this.dialogType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // sap仓库下拉数据
    getSapWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehouseSapPullListApi({}).then(res => {
          const { code, data } = res
          if (!code) {
            this.sapWarehousePullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 仓库下拉数据
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehousePullList1Api({}).then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.dialogType = 'modify'
      this.dialogVisible = true
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该${this.title}, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        getDeleteApi({
          id: item.id,
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.init()
            this.getSapWarehousePullList()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消删除',
          type: 'info'
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

